import * as THREE from "three";

/* create 3d scene */
var scene = new THREE.Scene();
/* create camera */
var camera = new THREE.PerspectiveCamera(
  75,
  window.innerWidth / window.innerHeight,
  0.1,
  1000
);
/* create renderer */
var renderer = new THREE.WebGLRenderer();
renderer.setSize(window.innerWidth, window.innerHeight);
document.body.appendChild(renderer.domElement);

// create a 3d spinning wireframe sphere
var geometry = new THREE.SphereGeometry(1, 32, 32);
var material = new THREE.MeshBasicMaterial({
  color: 0xffff00,
  wireframe: true
});
var sphere = new THREE.Mesh(geometry, material);
scene.add(sphere);
camera.position.z = 5;

// create a 3d spinning wireframe cube
var geometry = new THREE.BoxGeometry(1, 1, 1);
var material = new THREE.MeshBasicMaterial({
  color: 0x00ff00,
  wireframe: true
});
var cube = new THREE.Mesh(geometry, material);
scene.add(cube);
camera.position.z = 5;

// create a 3d spinning wireframe torus
var geometry = new THREE.TorusGeometry(1, 0.4, 16, 100);
var material = new THREE.MeshBasicMaterial({
  color: 0x0000ff,
  wireframe: true
});
var torus = new THREE.Mesh(geometry, material);
scene.add(torus);
camera.position.z = 5;

// create a 3d spinning wireframe cylinder
var geometry = new THREE.CylinderGeometry(1, 1, 1, 32);
var material = new THREE.MeshBasicMaterial({
  color: 0xff0000,
  wireframe: true
});
var cylinder = new THREE.Mesh(geometry, material);
scene.add(cylinder);
camera.position.z = 5;

//render
function render() {
  requestAnimationFrame(render);
  sphere.rotation.x += 0.01;
  sphere.rotation.y += 0.01;
  cube.rotation.x += 0.01;
  cube.rotation.y += 0.01;
  torus.rotation.x += 0.01;
  torus.rotation.y += 0.01;
  cylinder.rotation.x += 0.01;
  cylinder.rotation.y += 0.01;
  renderer.render(scene, camera);
}
render();
